	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Everybody Loves Anil","date":"28 Mar 2005","slug":"/2005-03-28-everybody-loves-anil/","tags":"Y2005"},"html":"<p><a title=\"Anil Dash: Everybody Loves Me!\" href=\"http://www.dashes.com/anil/2005/03/27/everybody_loves\">Everybody Loves Anil!</a> I bet you'd look great in his clothes.</p>"},"next":{"fields":{"title":"Ein Bier für Hartz IV","date":"28 Mar 2005","slug":"/2005-03-28-ein-bier-fr-hartz-iv/"}},"previous":{"fields":{"title":"greenpeace.spiegel.de","date":"28 Mar 2005","slug":"/2005-03-28-greenpeacespiegelde/"}}},{"node":{"fields":{"title":"Ein Bier für Hartz IV","date":"28 Mar 2005","slug":"/2005-03-28-ein-bier-fr-hartz-iv/","tags":"Y2005"},"html":"<p>I'm not sure how well this is known outside Germany, but the number one beer sold in Germany is no longer a national premium brand like Warsteiner, Bitburger or Krombacher, but the discount brand <a href=\"http://www.oettinger-bier.de/\">Oettinger</a>. Spiegel Online calls it <a title=\"Brauereien: Ein Bier f&uuml;r Hartz IV\" href=\"http://www.spiegel.de/spiegel/0,1518,348108,00.html\"><em>Ein Bier f&uuml;r Hartz IV</em></a>, after Schr&ouml;der's social reform package, since Oettinger usually sells at half the price of national brands. They cut costs by not advertising, selling only in stores (no bars or restaurants), and distributing themselves instead of through wholesalers. Interestingly, it's not sold at discount chains like Aldi... the profit margin there is too low.</p>"},"next":{"fields":{"title":"Otto fand ich gut","date":"27 Mar 2005","slug":"/2005-03-27-otto-fand-ich-gut/"}},"previous":{"fields":{"title":"Everybody Loves Anil","date":"28 Mar 2005","slug":"/2005-03-28-everybody-loves-anil/"}}},{"node":{"fields":{"title":"Otto fand ich gut","date":"27 Mar 2005","slug":"/2005-03-27-otto-fand-ich-gut/","tags":"Y2005"},"html":"<p>German mail-order and online merchant <a href=\"http://www.netzeitung.de/entertainment/style/331479.html\" title=\"NETZEITUNG: Otto k&uuml;ndigt Vertrag mit Heidi Klum\">Otto Versand fires Heidi Klum</a> for becoming pregnant, she <a href=\"http://www.heidiklum.de/do/newscenter/newseintrag.asp?id=200\" title=\"Heidi Klum GmbH: Otto fand ich gut\">announced</a> on her website. It's not like she doesn't have <a href=\"http://www.mcdonalds.de/\">other engagements</a>.</p>"},"next":{"fields":{"title":"Soccer Made In Germany","date":"26 Mar 2005","slug":"/2005-03-26-soccer-made-in-germany/"}},"previous":{"fields":{"title":"Ein Bier für Hartz IV","date":"28 Mar 2005","slug":"/2005-03-28-ein-bier-fr-hartz-iv/"}}}],"pathPrefix":"","first":false,"last":false,"index":331,"pageCount":1101}}

